* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; /* to avoid padding summing height (content-box) */
}

html {
  font-size: 62.5%;
}

.App {
  text-align: center;
}

.App-header {
  background-image: url("./mohmed-nazeeh--7UvUV2WoHc-unsplash.jpg");
  background-size: cover;
  background-position: top;
  float: left;
  width: 30%;
  height: 70vh;
}

.App-content {
  float: right;
  width: 70%;
  height: 100vh;
}

.App-footer {
  background-image: url("./mohmed-nazeeh--7UvUV2WoHc-unsplash.jpg");
  background-size: cover;
  background-position: bottom;
  float: left;
  width: 30%;
  height: 30vh;
}

@media screen and (min-width: 780px) and (min-width: 1024px) {
}

@media screen and (min-width: 480px) and (min-width: 780px) {
}

@media screen and (max-width: 480px) {
  .App-header {
    float: left;
    width: 100%;
    height: 70vh;
  }

  .App-content {
    float: left;
    width: 100%;
    height: 100vh;
  }

  .App-footer {
    float: left;
    width: 100%;
    height: 30vh;
  }
}

