.header {
  padding-bottom: 30vh;
  background-color: lightblue;
  position: relative;
}

.image {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateY(-50%);
  transform: translateX(-50%);
  height: 15em;
  border-radius: 100%;
  display: block;
}

.name {
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translateY(-50%);
  transform: translateX(-50%);
  color: white;
  font-size: 3em;
  align-content: center;
}
